* {
  box-sizing: border-box;
}

@font-face {
  font-family: centurygothic;
  src: url("centurygothic.0e62ac4e.ttf");
}

@font-face {
  font-family: BebasNeue;
  src: url("BebasNeue.224ef325.ttf");
}

body {
  max-width: 1920px;
  letter-spacing: .02em;
  box-sizing: border-box;
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
}

:root {
  --main-font: "centurygothic";
  --second-font: "BebasNeue";
  --white: #fff;
  --black: #242424;
  --brown: #c3a488;
  --grey: #5a5869;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

input, button, textarea, select {
  font: inherit;
  border: none;
  outline: none;
}

.h-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 1270px;
  box-sizing: border-box;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header {
  width: 100%;
  z-index: 5;
  transition: all .3s;
  position: fixed;
}

.header__scrolled {
  background-color: var(--brown);
}

.header__inner {
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  transition: all .3s;
  display: flex;
}

.header__style {
  padding: 15px 0;
}

.logotype {
  max-width: 145px;
}

.header__rht {
  align-items: center;
  gap: 76px;
  display: flex;
}

.header__menu {
  gap: 76px;
  display: flex;
}

.menu__item {
  font-family: var(--second-font);
  text-transform: uppercase;
  color: var(--white);
  font-size: 18px;
  font-weight: 400;
}

.menu__item:after {
  content: "";
  height: 1px;
  width: 0;
  background-color: var(--white);
  opacity: 0;
  margin: 0 auto;
  transition: all .3s;
  display: block;
}

.menu__item:hover:after {
  opacity: 1;
  width: 100%;
}

.header__burger {
  width: 22px;
  cursor: pointer;
}

.burger__item {
  height: 1px;
  background-color: var(--white);
  width: 22px;
  margin-bottom: 5px;
}

.burger__item_short {
  width: 18px;
  margin-left: auto;
}

.hero {
  position: relative;
}

.hero__video {
  width: 100%;
  max-width: 1920px;
  height: calc(100vh + 100px);
  object-fit: cover;
}

.play__video {
  width: 55px;
  height: 55px;
  margin: 0 auto 20px;
}

.video-link {
  font-size: 18px;
  font-family: var(--main-font);
  text-align: center;
  color: var(--white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.video-link:after {
  content: "";
  width: 0;
  height: 1px;
  background-color: var(--white);
  opacity: 0;
  margin: 0 auto;
  transition: all .3s;
  display: block;
}

.video-link:hover:after {
  opacity: 1;
  width: 100%;
}

.modal {
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, .8);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  width: 80%;
  max-width: 800px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.close-modal {
  color: #fff;
  cursor: pointer;
  font-size: 30px;
  position: absolute;
  top: 100px;
  right: 100px;
}

#videoModal video {
  max-width: 700px;
  width: 100%;
  height: auto;
}

