  color: var(--white);
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 100%;
}

.years__text:last-child {
  margin-bottom: 0;
}

.work {
  padding-bottom: 80px;
}

.work__title {
  font-family: var(--second-font);
  color: var(--black);
  text-align: center;
  margin-bottom: 20px;
  font-size: 50px;
  line-height: 100%;
