}

.work__big__bgd {
  width: 100%;
  height: 100%;
  background-color: var(--brown);
  position: absolute;
  top: 0;
  left: -50px;
}

.work__small__bgd {
  width: 100%;
  height: 100%;
  background-color: var(--brown);
  position: absolute;
  top: 0;
  left: 50px;
}

.work__big__photo, .work__small__photo {
  z-index: 2;
  position: relative;
}

.work__small_margin {
  margin-left: 30px;
}

.footer {
  background-color: var(--brown);
  padding: 95px 0 20px;
}

.footer__inner {
  display: flex;
}

.footer__logo {
  max-width: 145px;
  height: 51px;
  margin-top: -18px;
}

.footer__main {
  width: 100%;
  justify-content: space-between;
  margin-left: 145px;
