}

.footer__list__item:last-child {
  margin-bottom: 0;
}

.list_line {
  display: inline-block;
}

.list_line:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--black);
  opacity: 0;
  transition: all .3s;
  display: block;
}

.list_line:hover:after {
  opacity: 1;
}

.copyright {
  font-family: var(--main-font);
  color: var(--black);
  text-align: center;
  margin-top: 66px;
  font-size: 11px;
}

.footer__social {
  align-items: center;
  gap: 14px;
  margin-top: 50px;
  display: flex;
}

.social__item__btn {
  border: 1px solid var(--black);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.social__faceb {
  max-width: 13px;
}

.social__inst {
  max-width: 20px;
}

.banner {
  position: relative;
}

.banner__img {
  height: 80vh;
}

.banner__crumbs {
  align-items: center;
  display: flex;
  position: absolute;
  top: 110px;
}

.crumbs__item {
  align-items: center;
  display: flex;
}

.crumbs__item:after {
  content: "-";
  color: var(--white);
  margin: 0 5px 3px;
  display: block;
}

.crumbs__item:last-child:after {
  display: none;
}

.bread__crumb {
  font-family: var(--main-font);
  color: var(--white);
  align-items: center;
  font-size: 13px;
  line-height: 140%;
  display: flex;
}

.bread__crumb:hover {
  text-decoration: underline;
}

.bread__crumb_active {
  font-family: var(--main-font);
  color: var(--white);
  font-size: 13px;
  line-height: 140%;
}

.environment {
  padding: 110px 0 80px;
  position: relative;
}

.title {
  width: 100%;
  max-width: 809px;
  background-color: var(--white);
  padding: 38px 15px 10px 115px;
  position: absolute;
  top: -118px;
  left: 0;
}

.environment__title {
  font-family: var(--second-font);
  color: var(--black);
  font-size: 50px;
  line-height: 140%;
}

.environment__inner {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  display: flex;
}

.env__arrow {
  max-width: 85px;
  margin-top: 22px;
}

.photo__bgd_company {
  left: 47px;
}

.option__list {
  margin-bottom: 30px;
}

.option__list:last-child {
  margin-bottom: 0;
}

.option__title {
  font-family: var(--second-font);
  color: var(--brown);
  margin-bottom: 7px;
  font-size: 23px;
}

.option__title:after {
  content: "";
  height: 1px;
  width: 100%;
  max-width: 295px;
  background-color: var(--brown);
  display: block;
}

.option__txt {
  font-family: var(--main-font);
  color: var(--black);
  width: 100%;
  max-width: 620px;
  font-size: 17px;
}

.environment__inner .info__photo {
  margin-top: 100px;
  margin-right: 40px;
}

.criteria {
  padding-bottom: 80px;
}

.criteria__subtxt {
  font-family: var(--main-font);
  color: var(--black);
  text-align: center;
  margin-top: 15px;
  font-size: 20px;
  line-height: 100%;
}

.criteria__arrow {
  max-width: 85px;
  margin: 20px auto 50px;
}

.customers__list_company {
  margin-bottom: 0;
}

.technology {
  padding-bottom: 80px;
}

.technology__list {
  background: var(--white);
  height: 100%;
  justify-content: space-between;
  margin-top: -35px;
  display: flex;
}

.technology__item {
  background-color: var(--white);
  text-align: center;
  width: 100%;
  -webkit-clip-path: polygon(50% 15%, 65% 0, 100% 0, 100% 100%, 0 100%, 0 0, 35% 0);
  clip-path: polygon(50% 15%, 65% 0, 100% 0, 100% 100%, 0 100%, 0 0, 35% 0);
  margin: 0 -1px;
  padding-top: 30px;
}

.technology__arrow {
  max-width: 85px;
  margin: 0 auto 30px;
}

.technology__title {
  font-family: var(--second-font);
  color: var(--black);
  margin-bottom: 30px;
  font-size: 50px;
  line-height: 100%;
}

.technology__txt {
  font-family: var(--main-font);
  color: var(--black);
  text-align: center;
  max-width: 210px;
  margin: 0 auto;
  font-size: 16px;
  line-height: 100%;
}

.equipment {
  padding: 80px 0;
}

.equipment__inner {
  justify-content: space-between;
  gap: 30px;
  display: flex;
}

.content__title {
  text-align: right;
  font-family: var(--second-font);
  color: var(--black);
  margin-bottom: 13px;
  font-size: 50px;
  line-height: 140%;
}

.equipment__arrow {
  max-width: 85px;
  margin-bottom: 50px;
  margin-left: auto;
}

.equipment__txt {
  font-family: var(--main-font);
  color: var(--black);
  max-width: 610px;
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 140%;
}

.footer_serv {
  border-top: 1px solid var(--white);
}

.great {
  padding-bottom: 80px;
}

.servPage__title_w {
  max-width: 485px;
  padding: 38px 85px 10px 100px;
}

.info__services {
  padding: 150px 0 80px;
}

.great__inner {
  justify-content: space-between;
  align-items: center;
  gap: 60px;
  display: flex;
}

.great__inner .work__small {
  top: 0;
}

.question {
  padding-bottom: 80px;
}

.question__title {
  font-family: var(--second-font);
  color: var(--black);
  text-align: center;
  margin-bottom: 20px;
  font-size: 50px;
}

.question .env__arrow {
  margin: 0 auto 40px;
}

.answer__list {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.answer__item {
  width: 100%;
  max-width: 1030px;
  margin: 0 auto;
}

.answer__head {
  background-color: var(--brown);
  cursor: pointer;
  border-radius: 2px;
  justify-content: space-between;
  align-items: center;
  padding: 13px 20px;
  display: flex;
}

.head__title {
  font-family: var(--second-font);
  color: var(--white);
  font-size: 23px;
}

.head__plus {
  width: 17px;
  height: 17px;
  display: block;
}

.head__min {
  width: 17px;
  display: none;
}

.body__txt {
  font-family: var(--main-font);
  color: var(--black);
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 140%;
}

.body__txt:last-child {
  margin-bottom: 0;
}

.answer__body {
  margin: 20px 0;
  padding: 0 20px;
  transition: all 1.5s;
  display: none;
}

.answer__item_active .answer__body {
  display: block;
}

.answer__item_active .head__plus {
  display: none;
}

.answer__item_active .head__min {
  display: block;
}

.news {
  padding: 140px 0 80px;
  position: relative;
}

.news .info__photo {
  max-width: 347px;
  max-height: 347px;
}

.news__list {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 110px 20px;
  display: flex;
}

.news__item {
  width: calc(33.3333% - 40px);
}

.news__txt {
  margin-top: -25px;
}

.news__date {
  font-family: var(--main-font);
  color: var(--grey);
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 140%;
}

.news__item__title {
  font-family: var(--second-font);
  color: var(--black);
  margin-bottom: 20px;
  font-size: 23px;
  line-height: 100%;
}

.news__info {
  font-family: var(--main-font);
  color: var(--black);
  margin-bottom: 30px;
  font-size: 15px;
  line-height: 140%;
}

.news__item__btn {
  color: var(--white);
  font-family: var(--second-font);
  background: #c3a488;
  border-radius: 5px;
  padding: 10px 26px;
  font-size: 16px;
  line-height: 100%;
  transition: all .3s;
}

.news__item__btn:hover {
  opacity: .5;
  color: var(--black);
}

.pagination {
  justify-content: center;
  gap: 5px;
  margin-top: 68px;
  display: flex;
}

.news__eliosis, .news__pagination {
  font-family: var(--main-font);
  color: var(--brown);
  align-items: center;
  font-size: 16px;
  display: flex;
}

.news__pagination_prev {
  margin-right: 27px;
}

.news__pagination_next {
  margin-left: 27px;
}

.news__pagination_active {
  color: var(--black);
}

@media screen and (max-width: 1024px) {
  .header__menu, .header__rht {
    gap: 30px;
  }

  .info__txt {
    width: 100%;
  }

  .info__title {
    text-align: center;
    padding: 20px 10px;
    font-size: 38px;
    top: -80px;
  }

  .info__photo {
    max-width: 325px;
  }

  .inner__main {
    gap: 20px;
  }

  .photo__bgd_color {
    left: 15px;
  }

  .item__title {
    font-size: 32px;
  }

  .work__big {
    max-width: 325px;
  }

  .work__small {
    max-width: 290px;
  }

  .footer__main {
    margin-left: 30px;
  }

  .years__item {
    margin-right: 10px;
    font-size: 32px;
  }

  .arrow__down {
    right: calc(50% - 39px);
  }

  .work__small__bgd {
    left: 19px;
  }

  .environment__inner .info__photo {
    margin-right: 10px;
  }

  .header__menu, .header__rht {
    gap: 20px;
  }

  .banner__img {
    height: 60vh;
  }

  .title {
    text-align: center;
    padding: 15px 10px;
  }

  .environment__title {
    font-size: 40px;
  }

  .env__arrow {
    margin: 30px auto 0;
  }

  .environment {
    padding: 50px 0 80px;
  }

  .photo__bgd_company {
    left: 25px;
  }

  .customers__title {
    font-size: 40px;
  }

  .customers__why {
    padding: 26px 10px;
  }

  .technology__title {
    font-size: 40px;
  }

  .technology__arrow {
    max-width: 65px;
  }

  .content__title {
    font-size: 40px;
  }

  .logotype {
    max-width: 100px;
  }

  .footer__logo {
    max-width: 100px;
    height: 35px;
  }

  .footer__main {
    width: 100%;
    max-width: 400px;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 2fr;
    justify-content: space-between;
    gap: 30px;
    margin-left: auto;
